<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs-12 sm6>
        <v-btn large route to='/meetups' class="primary"> Explore Meetups</v-btn>
      </v-flex>
      
      <v-flex xs-12 sm6>
        <v-btn large route to='/meetup/new' class="primary"> Organize Meetup</v-btn>
      </v-flex>
    </v-layout>

    <v-layout>
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular 
        indeterminate 
        class="primary-text"
        :width="7"
        :height="70"
        v-if="loading"></v-progress-circular>
      </v-flex>
    </v-layout>
    
    <v-layout row wrap class="mt-2" v-if="!loading">
            <v-flex xs-12>
          <v-carousel style="cursor:pointer;">
            <v-carousel-item 
            v-for="meetup in meetups" 
            :src="meetup.imageUrl" 
            :key="meetup.id"
            @click="onLoadMeetup(meetup.id)">
              <div class="title">{{meetup.title}}</div>
            </v-carousel-item>
          </v-carousel>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
    //   data () {
    //   return {
    //     meetups: [
    //       {
    //         imageUrl: 'https://www.google.co.id/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png',id:'asdasdwraas11' ,title:'test title'
    //       },
    //       {
    //         imageUrl: 'https://www.google.co.id/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png',id:'asdasdwraas12' ,title:'test title2'
    //       },
    //       {
    //         imageUrl: 'https://www.google.co.id/images/branding/googlelogo/1x/googlelogo_color_272x92dp.png',id:'asdasdwraas13' ,title:'test title3'
    //       }
    //     ]
    //   }
    // },
    computed:{
      meetups () {
        return this.$store.getters.featuredMeetups
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    methods: {
      onLoadMeetup(id) {
        this.$router.push('meetups/' + id)
      }
    }
  }
</script>

<style scoped>
  .title{
    position: absolute;
    bottom: 50px;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 20px;
    font-size: 2em;
    color: white;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;    
  }
</style>




