<template>
    <v-dialog with="350" persistent v-model="editdialog">
        <v-btn fab accent slot="activator" class="primary">
            <v-icon>
                edit
            </v-icon>
             </v-btn>
            <v-card>
                <v-container>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card-title>Edit Meetup</v-card-title>
                        </v-flex>
                    </v-layout>
                    <v-divider></v-divider>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card-text>
                                <v-text-field
                                    name="title"
                                    label="title"
                                    id="title" 
                                    v-model="editedTitle"
                                    required>
                                    </v-text-field>

                                    <v-text-field
                                    name="description"
                                    label="Description"
                                    id="description" 
                                    textarea
                                    v-model="editedDescription"
                                    required>
                                    </v-text-field>
                            </v-card-text>
                        </v-flex>
                    </v-layout>
                    <v-divider></v-divider>

                    <v-layout row wrap>
                        <v-flex xs12>
                            <v-card-actions>
                                <v-btn flat color="primary" @click="onSaveChange">Save</v-btn>
                                <v-btn flat color="primary"  @click="editdialog = false">Close</v-btn>
                                </v-card-actions>
                        </v-flex>
                    </v-layout>                    
                </v-container>
            </v-card>
    </v-dialog>
</template>

<script>
export default {
  props:['meetup'],
  data () {
      return {
          editdialog:false,
          editedTitle: this.meetup.title,
          editedDescription: this.meetup.description
      }
  }, 
  methods: {
      onSaveChange () {
          if (this.editedTitle.trim() === '' || this.editedDescription.trim() === '') {
              return
          }
          this.editdialog = false
          this.$store.dispatch('updateMeetupData', {
              id:this.meetup.id,
              title:this.editedTitle,
              description:this.editedDescription
          })
      }
  }
}
</script>




