// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'

import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'

import App from './App'
import router from './router'
import {store} from './store'
import DateFilter from './filters/date'
import * as firebase from 'firebase'
import AlertCmp from './components/Shared/alert.vue'
import EditMeetupDetailDialog from './components/Meetup/Edit/EditMeetupDetailDialog.vue'
import EditMeetupDateDialog from './components/Meetup/Edit/EditMeetupDateDialog.vue'
import EditMeetupTimeDialog from './components/Meetup/Edit/EditMeetupTimeDialog.vue'

Vue.use(Vuetify, {
  theme: {
    primary: '#1976D2',
    secondary: '#424242',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})

Vue.filter('date', DateFilter)
Vue.component( 'app-alert', AlertCmp)
Vue.component('app-edit-meetup', EditMeetupDetailDialog)
Vue.component('app-edit-meetup-date', EditMeetupDateDialog)
Vue.component('app-edit-meetup-time', EditMeetupTimeDialog)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App },
  created() {
    firebase.initializeApp({
      apiKey: 'AIzaSyCS9CK-SO-cH9dDShqS8gSAsVBIcaqxCxo',
      authDomain: 'balinuxku.firebaseapp.com',
      databaseURL: 'https://balinuxku.firebaseio.com',
      projectId: 'balinuxku',
      storageBucket: 'gs://balinuxku.appspot.com',
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
      }
    })
    this.$store.dispatch('loadMeetups')
  }
})



