<template>
  <v-app >    
    <v-toolbar class="primary" dark>
      <v-toolbar-side-icon @click.stop="sideNav = !sideNav" class="hidden-sm-and-up"></v-toolbar-side-icon>
      <v-toolbar-title>YukLiqo</v-toolbar-title>

      <v-spacer></v-spacer>
      
      <v-toolbar-items>
        <v-btn 
        flat 
        v-for="toolbar in menuItems" 
        :key="toolbar.title"
        :to="toolbar.link">
          <v-icon left>{{toolbar.icon}}</v-icon> {{toolbar.title}}
          </v-btn>

        <v-btn 
        flat 
        v-if="userIsAuthenticated"
        @click="onLogout">
          <v-icon left dark>exit_to_app</v-icon>
          Log Out
          </v-btn>  
      </v-toolbar-items> 

    </v-toolbar>

        <main>
      <router-view></router-view>
    </main>

    <v-navigation-drawer
     v-model="sideNav"
     temporary
     light
     absolut>
      <v-list>
       <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/85.jpg" />
          </v-list-tile-avatar>
        
          <v-list-tile-content>
            <v-list-tile-title>John Leider</v-list-tile-title>
          </v-list-tile-content>
        
          <v-list-tile-action></v-list-tile-action>
        
        </v-list-tile>

        <!-- logout -->
        
      </v-list>

      <v-list class="pt-0" dense  v-for="menu in menuItems" :key="menu.title">
         <v-divider></v-divider>
        <v-list-tile >
            <v-list-tile-action>
                <v-icon> {{menu.icon}}</v-icon>  
              </v-list-tile-action> 

            <v-list-tile-content>
              <v-list-tile-title>{{menu.title}}</v-list-tile-title>
            </v-list-tile-content> 
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>    




  </v-app>
</template>

<script>
  export default {
    data () {
      return {
        sideNav:false,
        sideData:[
          {title:"menu 1" , icon:"dashboard"},
          {title:" menu 2", icon:"question_answer"}
        ]
      }
    },
    computed:{
      menuItems () {
        let menuItems = [
          {icon:'face', title:'Sign In',link:'/signin'},
          {icon:'lock_open', title:'Sign Up',link:'/signup'}
        ]
        if(this.userIsAuthenticated){
          menuItems = [
              {icon:'supervisor_account', title:'view meetup',link:'/meetups'},
              {icon:'room', title:'Organize meetup',link:'/meetup/new'},
              {icon:'person', title:'Profile',link:'/profile'}
          ]
        }
        return menuItems
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      }
    },
    methods: {
      onLogout () {
        this.$store.dispatch('logout')
      }
    }
  }
</script>




