<template>
  <v-container> 
    <v-layout row warp v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular 
        indeterminate 
        class="primary-text"
        :width="7"
        :height="70"
        v-if="loading"></v-progress-circular>
      </v-flex>
    </v-layout>

      <v-layout row wrap v-else>
          <v-flex xs12>
              <v-card>
              <!-- title -->
              <v-card-title class="primary--text"> 
                <h2>{{meetup.title}}</h2>
                <template v-if="userIsCreator"> 
                  <v-spacer></v-spacer>
                  <app-edit-meetup :meetup="meetup"></app-edit-meetup>
                </template>
                </v-card-title>

                <!-- media -->
              <v-card-media :src="meetup.imageUrl" height="400px">
              </v-card-media>                      

              <!-- Text-->
              <v-card-text>
                  <div class="info--text"> {{meetup.date | date}} - {{meetup.location}}</div>
                  <div>
                    <app-edit-meetup-date
                    :meetup="meetup" v-if="userIsCreator">
                    </app-edit-meetup-date>
                    <app-edit-meetup-time
                    :meetup="meetup" v-if="userIsCreator">
                    </app-edit-meetup-time>
                  </div>
                <div>
                  {{meetup.description}}
                </div>
              </v-card-text>
              <!-- action button -->
              <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn class="primary">
                      Register
                  </v-btn>
              </v-card-actions>
              </v-card>
          </v-flex>
      </v-layout>
  </v-container>
</template>

<script>
  export default {
    props: ['id'],
    computed: {
      meetup () {
        return this.$store.getters.loadedMeetup(this.id)
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
      userIsCreator () {
        if (!this.userIsAuthenticated) {
          return false
        }
        return this.$store.getters.user.id === this.meetup.creatorId
      },
      loading () {
        return this.$store.getters.loading
      }
    }
  }
</script>



